<template>
  <v-app
    class="pa-6 d-flex flex-column justify-center align-center"
    style="height: 100vh"
  >
    <v-dialog v-model="loginDialog" persistent width="500">
      <v-card>
        <v-card-title class="text-h5"> Budiž Heslo </v-card-title>
        <v-card-text class="mb-0">
          <v-text-field
            label="Heslo"
            type="password"
            v-model="loginInput"
            solo
            class="mb-0"
            tabindex="0"
          ></v-text-field>
          <div v-if="loginError" class="red--text">Špatné Heslo</div>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="loginClick()">
            Přihlásit se
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="newDialog" width="500">
      <v-card>
        <v-card-title class="text-h5"> Nový Týpek </v-card-title>
        <v-card-text class="mb-0">
          <v-text-field
            label="Jméno Přijmení"
            v-model="newDialogData.name"
            solo
            class="mt-3"
            tabindex="0"
          ></v-text-field>

          <v-btn-toggle
            dense
            v-model="newDialogData.adult"
            color="primary"
            class="mb-3 mr-3"
          >
            <v-btn :value="false">&lt;18 </v-btn>
            <v-btn :value="true">18</v-btn>
          </v-btn-toggle>
          <v-btn-toggle
            dense
            v-model="newDialogData.back"
            color="primary"
            class="mx-3 mb-3"
          >
            <v-btn :value="false">Jenom Tam</v-btn>
            <v-btn :value="true">Zpátečný</v-btn>
          </v-btn-toggle>
          <v-spacer></v-spacer>
          <v-btn-toggle
            dense
            v-model="newDialogData.money"
            color="primary"
            class="my-3"
          >
            <v-btn :value="12"> 12kč </v-btn>
            <v-btn :value="23"> 23kč </v-btn>
            <v-btn :value="49"> 49kč </v-btn>
            <v-btn :value="93"> 93kč </v-btn>
          </v-btn-toggle>
          <div class="text-body-1 mt-3">
            <u>Jenom Tam</u> <br />
            • 18 let bez ISIC - <b>49kč</b> <br />
            • Míň jak 18 let nebo (18 let s ISIC) - <b>12kč</b> <br />
            <u>Zpátečný</u> <br />
            • 18 let bez ISIC - <b>93kč</b> <br />
            • Míň jak 18 let nebo (18 let s ISIC) - <b>23kč</b>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="newSaveClick()"> Uložit </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="deleteDialog" persistent width="300">
      <v-card>
        <v-card-title class="text-h5"> Smazat Týpka? </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red" text @click="deleteDialog = false"> Ne </v-btn>
          <v-btn color="green" text @click="deleteItem()"> Ano </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dataDialog" width="500">
      <v-card>
        <v-app-bar flat color="rgba(0, 0, 0, 0)">
          <v-app-bar-nav-icon color="white"></v-app-bar-nav-icon>

          <v-toolbar-title class="text-h6 white--text pl-0">
            Messages
          </v-toolbar-title>

          <v-spacer></v-spacer>

          <v-btn color="white" icon>
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </v-app-bar>
      </v-card>
    </v-dialog>
    <div
      v-if="!loginDialog"
      class="d-flex justify-center align-center"
      style="height: 100vh"
    >
      <v-card
        style="max-width: 600px; width: 90vw; height: 95vh; max-height: 900px"
        class="elevation-3"
      >
        <v-toolbar color="grey darken-3" dark>
          <v-btn icon @click="loadData">
            <v-icon>mdi-reload</v-icon>
          </v-btn>
          <v-btn
            @click="dataDialog = true"
            depressed
            class="mx-3"
            color="grey darken-3"
            >Data</v-btn
          >
          <v-spacer></v-spacer>
          <v-btn icon @click="newDialog = true">
            <v-icon>mdi-plus</v-icon>
          </v-btn>
        </v-toolbar>
        <v-list
          class="elevation-1 ma-6 overflow-y-auto"
          two-line
          style="max-height: 80vh"
        >
          <v-list-item v-if="data.length === 0">
            <v-list-item-content>Nikdo nikde</v-list-item-content>
          </v-list-item>
          <v-list-item v-else v-for="d in data" :key="d.id">
            <v-list-item-content>
              <v-list-item-title v-text="d.name" />
              <v-list-item-subtitle>
                {{ d.adult ? "18" : "&lt;18" }} {{ d.back ? "+ Zpátečná" : "" }}
              </v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-action class="d-flex flex-row align-center">
              <div class="text-subtitle-1 mx-3">{{ d.money }}kč</div>
              <v-btn icon @click="deleteClick(d.id)">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-list-item-action>
          </v-list-item>
        </v-list>
      </v-card>
    </div>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  name: "App",

  data() {
    return {
      loginDialog: true,
      loginInput: "",
      loginError: false,
      token: null,
      breakpoint: "",
      data: [],
      newDialog: false,
      newDialogData: {
        name: "",
        back: false,
        adult: false,
        money: 12,
      },
      deleteDialog: false,
      deleteID: null,
      dataDialog: false,
    };
  },
  mounted() {},
  methods: {
    async loginClick() {
      let res = await axios
        .post("https://api.michalwinter.cz/2121/login", {
          password: this.loginInput,
        })
        .catch(() => {
          this.loginError = true;
          this.loginInput = "";
        });
      if (res.data) {
        this.token = res.data;
        this.loginDialog = false;
        this.loadData();
      }
    },
    deleteClick(id) {
      this.deleteDialog = true;
      this.deleteID = id;
    },
    async deleteItem() {
      let res = await axios.post("https://api.michalwinter.cz/2121/delete", {
        token: this.token,
        id: this.deleteID,
      });
      this.data = res.data;
      this.deleteDialog = false;
      this.deleteID = null;
    },
    async loadData() {
      let res = await axios.get(`https://api.michalwinter.cz/2121/`, {
        params: {
          token: this.token,
        },
      });
      this.data = res.data;
    },
    async newSaveClick() {
      let res = await axios.post("https://api.michalwinter.cz/2121/new", {
        token: this.token,
        name: this.newDialogData.name,
        money: this.newDialogData.money,
        adult: this.newDialogData.adult,
        back: this.newDialogData.back,
      });
      this.data = res.data;
      this.newDialogData = {
        name: "",
        back: false,
        money: 12,
        adult: false,
      };
      this.newDialog = false;
    },
  },
};
</script>
